import {Component, Host, h, Prop, Element} from '@stencil/core';

@Component({
  tag: 'storm-collapse-content',
  styleUrl: 'storm-collapse.css',
  shadow: false,
})

export class StormCollapseContent {

  @Element() host: HTMLElement;

  @Prop() id: string = "collapseExample";
  @Prop() content: string = "AAA";


  render() {
    return (
      <Host>
        <div class="collapse" id={this.id}>
          <div class="card card-body bg-gray-200">
            {this.content}
          </div>
        </div>
      </Host>
    );
  }

}
